import React from 'react'
import { Link } from 'gatsby'
import '../styles/typeCardBig.css'

export default function TypeCardBig(props) {
  return (
    <Link to='/'>
        <div className="type-card-big">
            <img src={props.cardImage} alt={props.imageAlt} />
            <div>
                <h2 className={props.titleColor}>{props.cardTitle}</h2>
                <p>{props.cardText}</p>
            </div>
        </div>
    </Link>
  )
}
